<template>
	<section class="nickname">
		<div class="forgetHead">
			<img src="../images/arrowright.png" @click="history"/>
			<span>修改昵称</span>
			<button @click='next'>保存</button>
		</div>
		<div class="modify">
			<input v-model='nickname' placeholder="请输入昵称" type="text" maxlength="10" />
		</div>
	</section>
  
</template>
<script>
	import { Toast } from 'mint-ui'
	import { nickName, getUser } from './../api/api'
	import { getStore } from './../config/mUtils'
export default {
  data () {
    return {
    	nickname: ''
    }
  },
  methods: {
  	history() { // 后退
		this.$router.push({path: '/personal'})
	},
	next() {
		if (this.nickname === '') {
			Toast({
				message: '请输入昵称',
				position: 'bottom',
				duration: 2000
			});
		} else {
			var userId = getStore('userId')
			let params = {
				userId: userId,
				realName: this.nickname
			}
			nickName(params).then(res => {
				if (res.code === 200) {
					Toast({
						message: res.msg,
						position: 'bottom',
						duration: 2000
					});
					this.$router.push({path: '/personal'})
				} else {
					Toast({
						message: res.msg,
						position: 'bottom',
						duration: 2000
					});
				}
				console.log(res.code)
			})
		}
	},
	init() {
		var userId = getStore('userId')
		let params = {
			userId: userId
		}
		getUser(params).then(res => {
			console.log(res)
			if (res.code === 200) {
				this.nickname = res.data.realName
			}
		})
	}
  },
  mounted() {
//	this.init()
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
.nickname {
	flex:1;
	z-index: 11;
	background: #f3f5f8;
	
	.forgetHead {
		width: 100%;
		background: white;
		position: relative;
		height: 0.8rem;
		line-height: 0.8rem;
		
		img {
			transform: rotateZ(180deg);
			vertical-align: middle;
			width: 0.15rem;
			margin-left: 0.2rem;
		}
		span:nth-child(2) {
			vertical-align: middle;
			font-size: 0.25rem;
			color: black;
			display: inline-block;
			width: 90%;
			text-align: center;
		}
		button:nth-child(3) {
			position: absolute;
			outline: none;
			border: 0;
			background: white;
			right: 0.2rem;
			top: 0.24rem;
			font-size: 0.25rem;
		}
	}
	.modify {
		width: 100%;
		height: 0.8rem;
		line-height: 0.8rem;
		background: white;
		margin-top: 0.2rem;
		
		input {
			width: 90%;
			height: 0.5rem;
			outline: none;
			border: 0;
			font-size: 0.25rem;
			margin-left: 0.2rem;
		}
	}
	
}

</style>



